import React, { useState } from 'react';
import './Experience.css';
import lifestages from '../../images/lifestages.jpg';
import drdo from '../../images/DRDO.jpg';
import delhiUni from '../../images/delhi_uni.jpg';
import vigyanshala from '../../images/vigyanshaala.jpg';
import thales from '../../images/thales.jpg';

const Experience = () => {
  const experiences = [
      {
        title: "Software Engineer",
        company: "LifeStages",
        location: "San Francisco, CA",
        date: "March 2024 - Present",
        description: [
          "Leading the development of the Android app from ground up, implementing cross-platform architecture and evaluating frameworks like React Native and Flutter.",
          "Enhancing the iOS app through feature development and deployment using CI/CD pipelines, directly impacting users' mental health and wellness journeys.",
          "Collaborating with cross-functional teams (product, design, engineering) to ensure seamless integration and cohesive user experience across platforms.",
          "Contributing to the full development lifecycle from ideation to deployment, with creative freedom to implement innovative features."
        ],
        image: lifestages,
        isPromotion: true,
        projects: [
          {
            title: "Sentiment Analysis of User journal to track user mental health",
            description: ["Utilized custom and fine tuned LLMs to analyze user journal entires to track and log their emotional status over a period of time.",
                         "Added some additional features to the app to help user retention and engagement, such as a mood tracker and streaks and badges as rewards.",
                         "The user's logged emotions are fed into another microserviceto create personalized modules and activities to help the user improve their mental health."],
            tags: ["Flutter", "Swift", "Firebase", "Analytics", "LLMs"],
            github: "https://github.com/siddhantripathi/Sentiment-analysis"
          },
          {
            title: "UI for android app",
            description: ["Building the UI for the android app using Android SDK and Flutter. Re-designed the app to have one working UI for both android and iOS.",
                        "The UI for the android app is currently in development, and the iOS app is already in production. The github link cannot be shared due to NDA."],
            
            tags: ["Android SDK", "Swift","Flutter", "Firebase", "Full-stack", "LLMs"],
            //github: "https://github.com/example/tracker"
          },
          {
            title: "Personalized Modules for User mental health",
            description: ["Utilizing the intput from various other microservices, including mood tracking, journal entries and user's goals, this microservice creates a personalized module for the user to help them improve their mental health. we use a custom LLM fine tuned to generate modules based on the user's input. The github link cannot be shared due to NDA."],
            tags: ["Flutter", "Swift", "Firebase", "Full-stack", "LLMs"],
            //github: "https://github.com/siddhantripathi/Sentiment-analysis"
          },
        ]
      },
    {
      title: "Software Engineer intern",
      company: "LifeStages",
      location: "San Francisco, CA",
      date: "March 2024 - June 2024",
      description:[ "Joined early in the development phase, transitioning a wellness app from initial concept to full execution. " +
                   "Developed a recommendation model using collaborative filtering with the data analysis team. " +
                   "Utilized Git for version control and adhered to SDLC principles, ensuring a smooth development process. " +
                   "Used CSS, JavaScript, and React for front-end and migrating from Firebase to AWS for scalability."],
      image: lifestages,
      isPromotionPrevious: true
    },
    {
      title: "Research Intern",
      company: "Center of Artificial Intelligence and Research DRDO",
      location: "Bangalore, India",
      date: "September 2021 - March 2022 ",
      description: ["Collaborated with a team on the Autonomous Ground Navigation System, achieving 98.5% precision localization in dynamic environments. " +
                   "Integrated and processed data from GPS, LIDAR, and camera, resulting in a robust 10km^2 terrain mapping. " +
                   "Maintained rigorous project documentation, facilitating smooth project handovers and future developments. "],
      image: drdo,
      isPromotionPrevious: false
      
    },
    {
      title: "Guest Lecturer",
      company: "Daulat Ram College, Delhi University",
      location: "Delhi, India",
      date: "October 2021 - November 2021 ",
      description:["Invited to lead a 2-week Python data analysis course for 45 students, covering machine learning libraries and receiving a 95% positive feedback rating. " +
                   "Assessed students to complete data analysis projects, improving their practical skills and knowledge. " ],
      image: delhiUni,
      isPromotionPrevious: false
    },
    {
      title: "Education Mentor",
      company: "VigyanShala, a non-profit organization",
      location: "Delhi, India",
      date: "August 2021 - October 2021 ",
      description: ["Led a team of 3 to deliver a Python curriculum to 30 students, emphasizing data analysis and visualization. "],
      image: vigyanshala,
      isPromotionPrevious: false
    },
    {
      title: "Data Analyst Intern",
      company: "Thales Group",
      location: "Delhi, India",
      date: "March 2021 - September 2021",
      description: ["Forecasted aircraft degradation with predictive modeling, raising maintenance scheduling accuracy by 15%. " +
                   "Enhanced data hygiene by organizing 50k+ client records, ensuring high accuracy for model-building. " +
                    "Conducted over 20 code reviews, reducing code defects by 10%." +
                    "Generated over 30 bi-weekly reports, translating data into actionable insights."],
      image: thales,
      isPromotionPrevious: false
    },
  ];

  const [expandedId, setExpandedId] = useState(null);
  const [expandedProjectId, setExpandedProjectId] = useState(null);

  const toggleExpand = (id) => {
    setExpandedId(expandedId === id ? null : id);
  };

  return (
    <section id="experience" className="experience">
      <h2>Experience</h2>
      <div className="experience-grid">
        {experiences.map((exp, index) => (
          <div 
            key={index} 
            className={`experience-card ${expandedId === index ? 'expanded' : ''} ${exp.isPromotion ? 'promotion' : ''} ${exp.isPromotionPrevious ? 'promotion-previous' : ''}`}
            onClick={() => toggleExpand(index)}
          >
            <div className="experience-preview">
              <img src={exp.image} alt={exp.company} className="company-logo" />
              <h3>{exp.title}</h3>
              <p className="company-name">{exp.company}</p>
            </div>
            
            {expandedId === index && (
              <div className="experience-details">
                <p className="date">{exp.date}</p>
                <p className="location">{exp.location}</p>
                <ul className="responsibilities">
                  {exp.description.map((item, i) => (
                    <li key={i}>{item}</li>
                  ))}
                </ul>
              </div>
            )}

            {expandedId === index && exp.projects && (
              <div className="company-projects">
                <h4>Projects at {exp.company}</h4>
                <div className="projects-grid">
                  {exp.projects.map((project, pIndex) => (
                    <div 
                      key={pIndex}
                      className={`project-card ${expandedProjectId === `${index}-${pIndex}` ? 'expanded' : ''}`}
                      onClick={(e) => {
                        e.stopPropagation();
                        setExpandedProjectId(expandedProjectId === `${index}-${pIndex}` ? null : `${index}-${pIndex}`);
                      }}
                    >
                      <h5>{project.title}</h5>
                      {expandedProjectId === `${index}-${pIndex}` && (
                        <>
                          <p>{project.description}</p>
                          <div className="project-tags">
                            {project.tags.map((tag, tIndex) => (
                              <span key={tIndex} className="tag">{tag}</span>
                            ))}
                          </div>
                          {project.github && (
                            <a 
                              href={project.github}
                              target="_blank"
                              rel="noopener noreferrer"
                              className="project-link"
                              onClick={(e) => e.stopPropagation()}
                            >
                              View on GitHub
                            </a>
                          )}
                        </>
                      )}
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        ))}
      </div>
    </section>
  );
};

export default Experience;